<template>
  <section>
    <nav>
      <ul>
        <li>
          <a
            class="tab-btn tab-frontend tab-active"
            @click="
              (contentFrontend = true), (contentVideo = false), tabFrontend()
            "
            ><i class="fas fa-code"></i> Frontend</a
          >
        </li>
        <li>
          <a
            class="tab-btn tab-video"
            @click="
              (contentFrontend = false), (contentVideo = true), tabVideo()
            "
            ><i class="fas fa-play"></i> Video</a
          >
        </li>
      </ul>
    </nav>

    <div class="head show-d">
      <div>Título</div>
      <div>Tecnologia</div>
      <div>Link</div>
    </div>

    <article
      v-show="contentFrontend"
      v-for="(item, article) in front"
      :key="'frontend' + article"
    >
      <div class="infos">
        <h2>{{ item.title }}</h2>
        <span>{{ item.stack }}</span>
        <a target="_blank" :href="item.url" class="btn btn-primary"
          ><i class="fas fa-external-link-alt"></i> Link</a
        >
      </div>
    </article>

    <article
      v-show="contentVideo"
      v-for="(item, article) in video"
      :key="'video' + article"
    >
      <div class="infos">
        <h2>{{ item.title }}</h2>
        <span>{{ item.stack }}</span>
        <a @click="showModal(item.url)" class="btn btn-primary"
          ><i class="far fa-eye"></i> Video</a
        >
      </div>
    </article>

    <div class="modal" v-show="modal" @click="offModal">
      <div class="modal-block">
        <iframe
          title="renato7oliveira"
          frameborder="0"
          height="500"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      contentFrontend: true,
      contentVideo: false,
      modal: false,

      front: [
        {
          title: "Tesla",
          description: "Header do site Tesla, usei Flex-Box.",
          stack: "Flex-box",
          url: "https://renato7oliveira.github.io/experimentos/Tesla/src/",
          screen: require("../assets/images/portfolio-tesla.jpg"),
        },
        {
          title: "Starbucks",
          description:
            "Réplica de um template da Nicepage, usei Flexbox e CSS-Grid.",
          stack: "CSS-Grid / Flex-box",
          url: "https://renato7oliveira.github.io/experimentos/Starbucks/src/",
          screen: require("../assets/images/portfolio-starbucks.jpg"),
        },
        {
          title: "Code Canyon",
          description:
            "Desafio de estruturar aqueles templates genéricos. Tratado espaços verticais com vh100 e vh50.",
          stack: "CSS-Grid",
          url: "https://renato7oliveira.github.io/experimentos/Grid_CSS_Template_Canyon/src/",
          screen: require("../assets/images/portfolio-template-canyon.jpg"),
        },

        {
          title: "Street Fighter Tributo",
          description:
            "Projeto interativo feito com Adobe Edge Animate (old school).",
          stack: "Adobe Edge Animate",
          url: "https://renato7oliveira.github.io/street-fighter-audio-project",
          screen: require("../assets/images/portfolio-street-fighter.jpg"),
        },
        {
          title: "Beatles Tributo",
          description: "Um pouco de CSS3 animate.",
          stack: "CSS3 animate / Vue.js",
          url: "https://renato7oliveira.github.io/experimentos/Beatles_Tributo/src/",
          screen: require("../assets/images/portfolio-beatles.jpg"),
        },
        {
          title: "E-mail Mkt",
          description:
            "E-mail marketing com HTML 1.0 e CSS inline (old school).",
          stack: "HTML 1.0",
          url: "https://renato7oliveira.github.io/experimentos/Email_Mkt/Vivo/index.html",
          screen: require("../assets/images/portfolio-frontend-emailmkt-vivo.jpg"),
        },
      ],
      video: [
        {
          title: "Game Play: KOF 96",
          stack: "Adobe Premiere",
          url: "https://www.youtube.com/embed/HWgCY43AHxc",
          screen: require("../assets/images/portfolio-video-kof-96.jpg"),
        },
        {
          title: "Game Play: KOF 95",
          stack: "Adobe Premiere",
          url: "https://www.youtube.com/embed/U8PxtB5k0J8",
          screen: require("../assets/images/portfolio-video-kof-95.jpg"),
        },
        {
          title: "Thug Life",
          stack: "Adobe Premiere",
          url: "https://www.youtube.com/embed/DJrxU-av8SI",
          screen: require("../assets/images/portfolio-video-thug-life.jpg"),
        },
        {
          title: "Breaking Bad: Slides de Fotos",
          stack: "Adobe Premiere",
          url: "https://www.youtube.com/embed/qHwl86jkmGg",
          screen: require("../assets/images/portfolio-breaking-bad-video.jpg"),
        },
      ],
    };
  },

  methods: {
    tabFrontend() {
      document.querySelector(".tab-frontend").classList.add("tab-active");
      document.querySelector(".tab-video").classList.remove("tab-active");
    },
    tabVideo() {
      document.querySelector(".tab-frontend").classList.remove("tab-active");
      document.querySelector(".tab-video").classList.add("tab-active");
    },
    showModal(url) {
      this.modal = true;
      document.querySelector("iframe").src = url;
    },
    offModal() {
      this.modal = false;
      document.querySelector("iframe").src = "";
    },
  },
};
</script>

<style lang="scss" scoped>
$color-primary: #141e55;
$background-hover: rgba(
  $color: #000,
  $alpha: 0.5,
);

nav {
  ul {
    display: inline-flex;
    i {
      font-size: 1.4em;
    }
    li {
      cursor: pointer;
      text-transform: uppercase;
      width: 100%;
      a {
        display: grid;
        align-items: center;
        text-align: center;
        height: 100px;
        width: 200px;
      }
    }
  }
}
.tab-btn {
  border: 1px solid rgba($color: $color-primary, $alpha: 0.3);
  color: $color-primary;
  &:hover {
    background: $color-primary;
    color: #fff;
  }
}

.tab-active {
  background: $color-primary;
  color: #fff;
}

.head {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  font-weight: bolder;
  padding: 4vh 0;
}

.head :nth-child(3) {
  text-align: center;
}

.infos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 25px;
  align-items: center;
  font-size: 1.1em;
  margin-bottom: 15px;
  h2 {
    font-size: inherit;
    font-weight: normal;
  }
  span {
    color: #999;
  }
  &:hover {
    background: rgba($color: $color-primary, $alpha: 0.1);
  }
}

.modal {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  align-items: center;
  background: #000;
  .modal-block {
    grid-column: 2 / 7;
    padding-top: 0px;
    iframe {
      width: 100%;
    }
  }
}

@media (max-width: 1024px) {
  .grid-default {
    .modal {
      .modal-block {
        grid-column: 1 / 7;
        iframe {
          width: 100%;
          height: 300px !important;
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .head,
  .infos {
    grid-template-columns: 1fr;
    grid-gap: 10px;
    border-bottom: 1px solid rgba($color: $color-primary, $alpha: 0.3);
    padding-bottom: 25px;
    margin: 25px;
    font-size: 0.9em;
    h2 {
      font-weight: bolder;
    }
  }

  nav {
    ul {
      display: flex;
      li {
        a {
          width: 100%;
        }
      }
    }
  }
}
</style>